export function xh() {
	let index = 0;
	let timer = 0;

	function move() {
		document.getElementsByTagName('ul')[0].style.transition = '.5s';
		document.getElementsByTagName('ul')[0].style.top = -30*index+'px';
	}

	function a() {
		index++;
		move()
		if (index == 6) {
		document.getElementsByTagName('ul')[0].style.transition = '0s';
		document.getElementsByTagName('ul')[0].style.top = '-0px';
		index = 0;
			
			
		}
	}

	function animate() {
		timer = setInterval( () => {
			a()
		
		},3000)
	}

animate()
	
}