<template>
	<div class="goods">
		 <div class="foodwrapper">
		 	<div class="left" ref="left">
		 		<ul>
		 			<li v-for="(item,index) in goods" :class="{'active':isActive === index}" @click="selectMenu(index)">
		 				<span>{{item.name}}</span>
		 				{{totalCount}}
		 			</li>
		 		</ul>
		 	</div>
		 	<div class="right" ref="right">
		 		<ul>
		 			<li v-for="item in goods" class="food-list-hook">
		 				<h1>{{item.name}}</h1>
		 				<ul>
		 					<li v-for="food in item.foods" @click="selectFood(food)">
		 						<div class="content">
		 							<div class="icon">
		 								<img :src="food.icon">
		 							</div>
		 							<div class="title">
		 								<div class="text">
		 									<p>{{food.name}}</p>
		 									<p>月售{{food.sellCount}}  赞{{food.ratings.length}}</p>
		 								</div>
		 								<div class="price">
		 									<div>￥{{food.price}}</div>
		 									<div class="oldprice" v-if="food.oldPrice">￥{{food.oldPrice}}</div>
		 								</div>
		 								<cartcontrol :food="food"></cartcontrol>
		 							</div>
		 						</div>
		 					</li>
		 				</ul>
		 			</li>
		 		</ul>
		 	</div>
		 </div>
		 <shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
		 <food ref="food" :food="selectedFood"></food>
		
	</div>
</template>

 <script>
 	import axios from 'axios';
 	import BScroll from 'better-scroll';
 	import shopcart from '../components/shopcart/shopcart.vue'
 	import cartcontrol from '../components/cartcontrol/cartcontrol.vue'
 	import food from '../components/food/food.vue'
 	export default{
 		name:'goods',
 		data(){
 			return{
 				goods:[],
 				listHeight:[],
 				scrollY:0,
 				clickEvent:false,
 				selectedFood:[],
 				foodbox: false
 			}
 		},
 		props:{
			seller:{
				type: Object
			}
		},
 		mounted(){
	      axios.get('./data.json').then((res) =>{
	        this.goods = (res.data.goods);
	        this.$nextTick(() => {
	        	this._initScroll();
	        	this._getHeight();
	        });
	      })
	    },
	    components:{
	    	shopcart,
	    	cartcontrol,
	    	food
	    },
	    methods:{
	    	_initScroll(){
	    		this.left = new BScroll(this.$refs.left, {
	    			click : true
	    		})
	    		this.right = new BScroll(this.$refs.right, {
	    			click : true,
	    			probeType: 3
	    		})
	    		this.right.on('scroll',(pos) => {
	    			this.scrollY = Math.abs(Math.round(pos.y))
	    		})
	    	},
	    	_getHeight(){
	    		let rightItems = this.$refs.right.getElementsByClassName('food-list-hook');
	    		let height = 0;
	    		this.listHeight.push(height);
	    		for(let i = 0;i<rightItems.length; i++){
	    			height += rightItems[i].clientHeight;
	    			this.listHeight.push(height)
	    		}
	    	},
	    	selectMenu(index){
	    		let rightItems = this.$refs.right.getElementsByClassName('food-list-hook');
	    		let el = rightItems[index];
	    		this.right.scrollToElement(el, 300)
	    		console.log(index)
	    	},
	    	selectFood(food){
	    		if (!event._constructed) {
		          return;
		        }
	    		this.clickEvent = true
    			this.selectedFood = food;
    			this.foodbox = true
    			this.$refs.food.show()
    		},
	    },
	    computed:{
	    	isActive(){
	    		for(let i =0;i<this.listHeight.length;i++){
	    			let height1 = this.listHeight[i];
	    			let height2 = this.listHeight[i+1];
	    			if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
	    				return i
	    			}
	    			if (this.listHeight[this.listHeight.length - 1] - this.$refs.right.clientHeight <= this.scrollY) {
	    				return (this.listHeight.length - 1)
	    			}
	    		}
	    		return 0
	    	},
	    	selectFoods(){
	    		let foods = [];
	    		this.goods.forEach((good) => {
	    			good.foods.forEach((food) => {
	    				if (food.count) {
	    					foods.push(food)
	    				}
	    			})
	    		}) ;
	    		return foods
	    	}
	    },
 	}
 </script>

 <style lang="scss">
 	.goods{
 		.foodwrapper{
 			width: 100%;
 			top: 190px;
 			overflow: hidden;
 			position:absolute;
 			z-index: -1;
 			bottom:46px;
 			display: flex;
 			.left{
 				flex: 0 0 80px;
 				width: 80px;
 				height: auto;
 				background: #f5f5f5;
 				ul{
 					li{
 						height: 50px;
 						padding: 10px;
 						box-sizing:border-box;
 						span{
 							font-size: 13px;
 						}
 						&.active{
 							background:#fff;
 						}
 					}
 				}
 			}
 			.right{
 				flex: 1;
 				padding-left: 10px;	
 				ul{
 					li{
 						h1{
 							font-size: 12px;
 							height: 36px;
 							line-height: 36px;
 							color: #333333;
 							font-weight: 300;
 						}
 						ul{
 							li{
 								.content{
 									display: flex;
 									height: 107px;
 									padding:0 10px 22px 0;
 									font-size: 16px;
	 								font-weight: 700;
	 								position: relative;
 									.icon{
	 									img{
	 										width: 75px;
	 										height: 75px;
	 										margin-right: 10px;
	 									}
 									}
	 								.title{
	 									line-height: 21px;
		 								.text{
		 									height: 55px;
		 									p{
		 										color: #333;
		 										&:last-child{
		 											font-size: 12px;
		 											color: #666;
		 											font-weight: 300;
		 										}
		 									}
		 								}
		 								.price{
		 									height: 20px;
		 									line-height: 20px;
		 									div{
		 										color: #fb4e44;
		 										display: inline-block;
		 									}
		 									.oldprice{
		 										color: #a9a9a9;
		 										text-decoration: line-through;
		 										font-size: 11px;
		 										margin-left: 4px;
		 										vertical-align: middle;
		 									}
		 								}
	 								}
 								}
 								
 							}
 						}
 					}
 				}
 			}
 		}
 		
 	}
 </style>