<template>
	<div class="shopcart">
		<div class="content">
			<div class="left">
				<div class="icon" :class="{'highlight' : totalCount > 0}" @click="toggleList()">
					<div :class="{'highlight' : totalCount > 0}">{{totalCount}}</div>
				</div>
				<div class="text">
					<div v-show="totalPrice > 0" class="price">
						<div>￥</div>
						<div>{{totalPrice}}</div>
					</div>
					<div class="desc" :class="{'highlight' : totalPrice > 0}">另需配送费{{deliveryPrice}}元</div>
				</div>
			</div>
			<div class="right" :class="{'highlight': totalPrice > 0,'highlight1': totalPrice > minPrice}" >
				<div class="pay" @click='pay()'>
					 {{payDesc}}
				</div>
			</div>
		</div>
		<div class="trheader"  v-if="totalPrice > minPrice-5" v-show="headershow">
				<div v-if="totalPrice > minPrice-5 && totalPrice < minPrice">还差</div>
				<div >{{trboxpayDesc}}</div>
				<div v-if="totalPrice > minPrice-5 && totalPrice < minPrice">就能起送</div>
				<div v-if="totalPrice > minPrice-5 && totalPrice < minPrice">去凑单</div>
		</div>
		<transition name="fold">
			<div class="trbox" v-show="listShow">
				<div class="mid" v-if="totalPrice > minPrice-5">
					<h1>购物车</h1>
					<div class="menu" @click="menuclick()">
						清空购物车
					</div>
				</div>
				<div class="gwcbox">
					<div class="list">
						<ul>
							<li v-for="food in selectFoods">
								<span>{{food.name}}</span>
								<span>￥{{food.price * food.count}}</span>
								<span class="cartcontrol">
									<cartcontrol :food = "food"></cartcontrol>
								</span>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import cartcontrol from '../cartcontrol/cartcontrol.vue';
	export default{
		name:'shopcart',
		props:{
			selectFoods:{
				type: Array,
				default(){
					return[];
				}
			},
			deliveryPrice:{
				type: Number,
				default: 0
			},
			minPrice:{
				type: Number,
				default: 0
			}
		},
		data(){
			return{
				headershow:true,
				listShow: false
			}
		},
		components:{
			cartcontrol
		},
		computed:{
			totalPrice(){
				let total = 0;
				this.selectFoods.forEach((food) => {
					total += food.price * food.count
				});
				return total;
			},
			totalCount(){
				let count = 0;
				this.selectFoods.forEach((food) => {
					count +=  food.count
				});
				return count;
			},
			payDesc(){
				if (this.totalPrice === 0) {
					return `￥${this.minPrice}元起送`; 
				}else if (this.totalPrice < this.minPrice) {
					let diff = this.minPrice - this.totalPrice;
					return `差${diff}元起送`;
				}else{
					return `去结算`;
				}
			},
			trboxpayDesc(){
				if (this.totalPrice > (this.minPrice - 5) && this.totalPrice < this.minPrice) {
					let diff = this.minPrice - this.totalPrice;
					return `${diff}元`
				}else{
					return `以满足起送价`
				}
			},
			menuclick(){
				this.selectFoods.forEach((food) =>{
					food.count = 0;
				})
			},
			listShow(){
				if (!this.totalCount) {
					this.listShow = true;
				return false;
				}
			}
		},
		methods:{
			toggleList(){
				if(!this.totalCount){
					return;
				}
				this.headershow = !this.headershow
				this.listShow = !this.listShow
			},
			pay(){
				if (this.totalPrice < this.minPrice) {
					return
				}
				alert(`支付${this.totalPrice}元成功`)
				this.selectFoods.forEach((food) =>{
					food.count = 0;
				})
			}
		}
	}
</script>

<style lang="scss">
	.shopcart{
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;

		height: 46px;
		background:#3b3b3c;
		.content{
			display: flex;
			color: #999;
			.left{
				flex: 1;
				display: flex;
				.icon{
					margin: 0 10px;
					border-radius: 50%;
					width: 46px;
					height: 46px;
					background: url('./2.png');
					background-size:contain;
					position:relative;
					top: -10px;
					&.highlight{
						background: url('./1.png');
						background-size:contain;
					}
					div{
						width: 20px;
						height: 15px;
						background:#FB4E44;
						color: #fff;
						border-radius:15px;
						position:absolute;
						right: 0;
						top: 0;
						display: none;
						font-size: 10px;
						line-height: 15px;
						text-align: center;
						&.highlight{
							display: block;
						}
					}
				}
				.text{
					.price{
						color: #fff;
						font-size: 24px;
						div{
							margin-top: 3px;
							display: inline-block;
							&:first-child{
								font-size: 16px;
								margin-right: 2px;
								vertical-align: bottom;
							}
						}
					}
					.desc{
						font-size: 14px;
						line-height: 50px;
						&.highlight{
							font-size: 12px;
							line-height: 12px;
						}
					}
				}
			}
			.right{
				font-size: 16px;
				padding: 0 16px;
				font-weight: 700;
				line-height: 46px;
				&.highlight{
					background:#2e2d2d;
				}
				&.highlight1{
					background:#f8c74e;
					color: black;
				}
			}
		}
		.trheader{
			position:absolute;
			top: -25px;
			z-index: -1;
			width: 100%;
			height: 25px;
			line-height: 25px;
			background:#fff1d0;
			text-align: center;
			letter-spacing:0.15px;
			font-size: 11px;
			color: #333;
			div{
				display: inline-block;
				&:nth-child(2){
					color: #FB4E44;
				}
				&:nth-child(4){
					color: white;
					background:#FB4E44;
					margin-left: 8px;
					border-radius:2px;
					width: 58px;
					height: 20px;
					vertical-align: middle;
					line-height: 20px;
				}
			}
		}
		.trbox{
			width: 100%;
			position:absolute;
			top: 0;
			left: 0;
			background:white;
			z-index: -1;
			transform: translate3d(0,-100%,0);
			&.fold-enter-active,&.fold-leave-active{
		    	transition: all .5s;
		    }
		    &.fold-enter,&.fold-leave-to{
		        transform: translate3d(0,0,0);
		    }
			.mid{
				height: 30px;
				line-height: 30px;
				background:#fff1d0;
				padding:4px 15px;
				font-size: 12px;
				h1{
					font-size: 12px;
					font-weight: 300;
					float: left;
				}
				.menu{
					float: right;
					position:relative;
					&:before{
						content: '';
						width: 13px;
						height: 30px;
						background:url('./3.png');
						background-size:contain;
						background-repeat:no-repeat;
						background-position:center;
						display: inline-block;
						margin-right: 6px;
						vertical-align: bottom;
					}
				}
			}
			.gwcbox{
				max-height:250px;
				overflow: auto;
				background:#fff;
				li{
					height: 60px;
					padding:0 14px;
					border-bottom:1px solid rgba(0,0,0,.1);
					position:relative;
					span{
						height: 60px;
						line-height: 60px;
					}
					.cartcontrol{
						height: 60px;
						display: flex;
						position:absolute;
						top: 0;
						.cart-decrease,.cart-count,.cart-add{
							margin:auto;
						}
					}
				}
			}
		}
}
</style>