<template>
	<div class="header">
		<div class="comeback">
			<img src="./comeback.png">
		</div>
		<div class="wrapper">
			<div class="avatar">
				<img :src="seller.avatar">
			</div>
			<div class="content">
				<div class="text">
					<p>{{seller.deliveryTime}}分钟 | {{seller.score}}KM</p>
					<p>公告:{{seller.bulletin}}</p>
				</div>
				<div class="xh">
					<ul class="title" @click="tanchu()">
					<li v-for="(item, index) in seller.supports">
						<span class="icon" :class="iconclass[seller.supports[index].type]"></span>
						<span>{{seller.supports[index].description}}</span>
					</li>
					<li>
						<span class="icon decrease"></span>
						<span>在线支付满28减5</span>
					</li>
				</ul>
				</div>
			</div>
		</div>
		<div class="tanchu" v-show="tanchur">
			<div class="tanchubg" @click="tanchubg()">
				<div class="tanchubox">
					<ul class="item">
						<li v-for="(item, index) in seller.supports">
							<span class="icon" :class="iconclass[seller.supports[index].type]"></span>
							<span>{{seller.supports[index].description}}</span>
						</li>
					</ul>
					<div class="item">
						<p>配送</p>
						<p>起送{{seller.minPrice}}￥</p>
						<p>{{seller.description + '/' + seller.deliveryTime + '分钟送达'}}</p>
					</div>
					<div class="item">
						<p>公告</p>
						<p>{{seller.bulletin}}</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {xh} from '../../assets/js/header.js'

	export default{
		name:'header',
		props: {
			seller:{
				type: Object
			}
		},
		data(){
			return{
				tanchur:false
			}
		},
		created(){
			this.iconclass = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
			xh();
		},
		methods:{
			tanchu(){
				this.tanchur = true
				console.log('s')
			},
			tanchubg(){
				this.tanchur = false
			}
		}	
	}
</script>

<style lang="scss">
@mixin bg-image($url) {
    background-image: url($url + "@2x.png");
    @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
        background-image: url($url + "@3x.png");
    }
}
	.header{
		width: 100%;
		height: 150px;
		.comeback{
			width: 100%;
			height: 50px;
			background-color:#2E2F3B;
			padding: 15px 0 0 15px ;
			box-sizing:border-box;
			img{
				width: 16px;
				height: 16px;
			}
		}
		.wrapper{
			display: flex;
			height: 80px;
			width: 100%;
			background-color:#2E2F3B;
			padding: 5px 0 0 15px;
			box-sizing:border-box;
			.avatar{
				flex: 0 0 85px;
				img{
					width: 85px;
					height: 85px;
					border-radius:2px;
					box-shadow:0 2px 15px 0 rgba(0, 0, 0, 0.15);
				}
			}
			.content{
				flex: 1;
				height: 75px;
				.text{
					margin-left: 5px;
					color: white;
					height: 45px;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					vertical-align: top;
					p{
						font-size: 12px;
						height: 21px;
						line-height: 16px;
					}
				}
				.xh{
					height: 30px;
					position:relative;
					background-color:rgba(255,255,255,.2);
					overflow: hidden;
					.title{
						position: absolute;
						margin-left: 5px;
						top: 0;
						transition: 1s;
						li{
							.icon{
								display: inline-block;
								width: 16px;
								height: 16px;
								vertical-align: middle;
								margin-right: 6px;
								background-size:16px 16px;
								background-repeat: no-repeat;
								&.decrease{
									@include bg-image('decrease_3');
								}
								&.discount{
									@include bg-image('discount_3');
								}
					            &.guarantee{
					            	@include bg-image('guarantee_3');
					            }
					            &.invoice{
					            	@include bg-image('invoice_3');
					            }
					            &.special{
					            	@include bg-image('special_3');
					            }
							}
							span{
								height: 30px;
								line-height: 30px;
								font-size: 12px;
								color: #fff;
								font-weight: 700;
							}
						}
					}
				}
			}
		}
		.tanchu{
			width: 100%;
			height: 100%;
			position:absolute;
			bottom: 0;
			z-index: 99;
			.tanchubg{
				width: 100%;
				height: 100%;
				background-color:rgba(0,0,0,.4);
			}
			.tanchubox{
				position:absolute;
				bottom:0;
				width: 100%;
				height: auto;
				padding:20px 15px 35px;
				box-sizing:border-box;
				background-color:white;
				.item{
					margin-bottom:20px; 
					li{
						.icon{
						display: inline-block;
						width: 15px;
						height: 15px;
						vertical-align: middle;
						margin-right: 6px;
						background-size:15px 15px;
						background-repeat: no-repeat;
							&.decrease{
								@include bg-image('decrease_3');
								}
							&.discount{
								@include bg-image('discount_3');
								}
					        &.guarantee{
					            @include bg-image('guarantee_3');
					        	}
					        &.invoice{
					            @include bg-image('invoice_3');
					       		}
					        &.special{
					            @include bg-image('special_3');
					            }
							}
							span{
								height: 15px;
								line-height: 15px;
								font-size: 12px;
							}
						}
					}
					p{
						font-size: 12px;
						&:first-child{
							font-size: 16px;
							margin: 2px 0;
						}
					}
				}
		}
	}
</style>