<template>
	<div class="cartcontrol">
		<div v-show="food.count>0" class="cart-decrease"  @click.stop.prevent="decrease()"></div>
		<div v-show="food.count>0" class="cart-count">{{food.count}}</div>
		<div class="cart-add" @click.stop.prevent="addcart()"></div>
	</div>
</template>

<script>
	import Vue from 'vue'
	export default{
		name:'cartcontrol',
		props:{
	      food:{
	        type: Object
	      }
	    },
	    methods:{
	    	addcart(){
	    		if (!this.food.count) {
		          Vue.set(this.food, 'count', 1)
		        }else{
		          this.food.count++
		        }
	    	},
	    	decrease(){
	    		if (this.food.count) {
		          this.food.count--
		        }
	    	}
	    }
	}
</script>

<style lang="scss">
	.cartcontrol{
		position: absolute;
		right: 10px;
		top: 62.5px;
		width: auto;
		height: 25px;
		text-align: center;
		.cart-decrease,.cart-count,.cart-add{
			display: inline-block;
			width: 25px;
			height: 25px;
			border-radius: 50%;
		}
		.cart-decrease{
			background:url('./1.png');
			background-size: contain;
		}
		.cart-count{
		 	font-size: 16px;
		 	color: #333333;
		 	padding: 0 4px;
		 	line-height: 30px;
		 	vertical-align: top;
		 }
		.cart-add{
			background:url('./0.png');
			background-size: contain;
		}
	}
</style>